*{
  margin: 0px;
  font-size: 62.5%;
  font-family: 'Nunito', sans-serif;

} 

body{
  width: 100%;
  height: 100vh;
  background: black;
  color: white;
  display: grid;
  place-items: center;
}

#root{
  width: 60%;
  height: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
  place-content: center;
  align-items: center;
  border: 2px solid #FAB132;
  border-radius: 25px;
  padding: 28px;
  position: relative;
  overflow: hidden;
}

