#modal  textarea{
  height: 60px;
  width: 300px;
}
::placeholder{
  font-size: 2rem;
}

input{
  font-size: 1.8rem;
  width: 100%;
  height: 8%;
  border: 2px solid #FAB132;
  border-radius: 10px;
  padding: 10px;
  background: transparent;
  outline: none;
  color: white;
}

::placeholder{
  color: #FAB132;
  opacity: 0.5;
}


#createbutton{
  width: 110%;
  font-size: 1.8rem;
  background:#FAB132;
  position: absolute;
  bottom: 0px;
  border: none;
  height: 8%;
  color: rgb(5, 5, 5);
  font-weight: 700;
  cursor: pointer;
  z-index: 1;
}

.AddToDo{
  position: absolute;
  width: 80%;
  height: 10%;
  border: 2px solid white;
  border-radius: 10px;
  padding: 10px;
  background: white;
  transform: translateZ(5px) scale(1);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.ToDoInput{
  font-size: 1.8rem;
  width: 80%;
  height: 10%;
  border: 2px solid white;
  border-radius: 10px;
  padding: 10px;
  background: white;
  color: black;
  
}

.ToDoInput::placeholder{
  color: black;
  
}

.ToDoInputButton{
  font-size: 1.5rem;
  padding: 10px;
  border-radius: 10px;
  background: white;
  border: solid 2px greenyellow;
}

.hide{
  display: none;
}

p{
  text-align: center;
  font-size: 2.5rem;
}

