li{
    display: flex;
    gap: 10px;
    width: 100%;
    justify-content: space-between;
    font-size: 1rem;
}

ul{
    width: 100%;
    padding: 24px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    transform: scaleZ(1px);
    align-content: center;
    flex-wrap: wrap;
}

.textitem{
    font-size: 2rem;
    
}
.textitemDone{
    text-decoration:line-through;
    color: gainsboro;
    opacity: 0.6;
}

.icon{
    cursor: pointer;
    width: 5%;
    
}

.checkDone{
    filter: grayscale(1);
}