#loading-container{
    display: flex;
    gap: 30px;
    
    align-content: center;
    position: relative;
    padding: 40px;
}

.loading{
    border-radius: 50%;
    height: 50px;
    width: 50px;
    background: rgba(241, 225, 45, 0.594);
}

.jumper{
    background: rgb(241, 225, 45);
    position: absolute;
    animation: salto 1s infinite;
}

.l1{
    animation: pintando1 1s infinite ease-in-out;
}
.l2{
    animation: pintando2 1s infinite;
}
.l3{
    animation: pintando3 1s infinite ease-in-out;
}
.l4{
    animation: pintando4 1s infinite;
}

@keyframes salto {
    0%{
        transform: translateX(0px);
        
    }
    22%{
        transform: translateX(40px) translateY(-60px);
    }
    25%{
        transform: translateX(80px);   
    }
    47%{
        transform: translateX(120px) translateY(-60px);
    }
    50%{
        transform: translateX(160px);
    }
    72%{
        transform: translateX(200px) translateY(-60px);
    }
    75%{
        transform: translateX(240px);
    }
    97%{
        transform: translateX(280px) translateY(-60px);
    }
    100%{
        transform :scale(0);
    }
}

@keyframes pintando1{
    0%{
        background:  rgb(241, 225, 45); 
    }
    100%{
        background:  rgb(241, 225, 45); 
    }
}

@keyframes pintando2{
    0%{
        background: rgba(241, 225, 45, 0.594);
    }
    24%{
        background: rgba(241, 225, 45, 0.594);
    }
    25%{
        background:  rgb(241, 225, 45); 
    }
    100%{
        background:  rgb(241, 225, 45); 
    }
}

@keyframes pintando3{
    0%{
        background:  rgba(241, 225, 45, 0.594);
    }
    48%{
        background:  rgba(241, 225, 45, 0.594);
    }
    
    49%{
        background:  rgb(241, 225, 45); 
    }
    100%{
        background:  rgb(241, 225, 45); 
    }
}

@keyframes pintando4{
    0%{
        background:  rgba(241, 225, 45, 0.594);
    }
    74%{
        background:  rgba(241, 225, 45, 0.594);
    }
    
    75%{
        background:  rgb(241, 225, 45); 
    }
    100%{
        background:  rgb(241, 225, 45); 
    }
}